// necessary to parse POST data
var bodyparser = Npm.require('body-parser');
// necessary for Collection use and other wrapped methods
var Fiber = Npm.require('fibers');


WebApp.connectHandlers.use(bodyparser.json());
WebApp.connectHandlers.use(bodyparser.urlencoded({
    extended: false
}));
WebApp.connectHandlers.use('/tasks', function(req, res, next){
    var orderID = req.query.orderID;
    var category = req.query.category;
    if (category == "null" || category == "undefined") category = null; //Seriously, why the fuck do I have to do this?!?!
        if(req.method == "GET"){
        sendTasks(res, orderID, category);
        res.end();
    }
    else if (req.method == "POST"){
        updateTasks(orderID, JSON.parse(req.body.tasks), category);
        res.end(JSON.stringify({
            result: "OK"
        }));
    }
});

function sendTasks(res, orderID, category){
    res.writeHead(200, {
        'Content-Type': 'application/json'
    });
    var tasks;
    if(category){
        tasks = get_category_template(category, new Date());
    }
    else {
        var order = Orders.findOne(orderID);
        tasks = order.tasks;
        if(!tasks){
            tasks = get_category_template(order.category, order.entryDate);
            console.log("Serving category template");
            updateTasks(orderID, tasks);
        }
        else {
            console.log("Serving order");
        }
    }
    return res.write(JSON.stringify(tasks));
    var order = Orders.findOne(orderID);
    if(!order.tasks){
        var tasks = generate_dummy(order.entryDate);
        updateTasks(orderID, JSON.stringify(tasks));
        return res.write(JSON.stringify(tasks));
    }
    res.write(JSON.stringify(order.tasks));
}

function updateTasks(orderID, tasks, category){
    Fiber(function(){
        if(category){
            Categories.update({
                description: category,
            }, {
                $set: {
                    tasks: tasks
                }
            });
            return;
        }
        Orders.update(orderID, {
            $set: {
                tasks: tasks
            }
        });
    }).run();
}

function get_category_template(category_desc, startDate){
    var category = Categories.findOne({
        description: category_desc
    });
    if(!category){
        console.log("No category found for: "+category_desc);
        return null;
    }
    if(category.tasks){
        var tasks = category.tasks;
        moveProject(tasks, startDate);
        console.log("Category " + category_desc + " has a template");
        return category.tasks;
    }
    var dummy = generate_dummy(startDate, category_desc);
    Fiber(function(){
        Categories.update(category._id, {
            $set: {
                tasks: dummy
            }
        });
    }).run();
    return dummy;
}

function generate_dummy(startDate, category){
    var dummy = JSON.parse(JSON.stringify(dummy_project));
    if(!startDate){
        startDate = new Date();
    }
    if(category) dummy.tasks[0].name = category;
    moveProject(dummy, startDate);
    return dummy;
}

function moveProject(project, startDate){
    var deltaTime = startDate.getTime() - project.tasks[0].start;
    project.tasks.forEach(function(task){
        task.start += deltaTime;
        task.end += deltaTime;
    });
}

